:root {
  --white: #ffffff;
  --regularGrey: #dbdada;
  --black: #000000;
  --darkBlue: #273849;
  --darkPink: #EF426F;
  --vueGreen: #42b983;
  --yellow: #ffc107;
  --weight: 400;
  --radius: 5px; }

*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box; }

body {
  font-family: 'Asap', sans-serif;
  font-weight: var(--weight); }
  body h1 {
    --weight: 700;
    font-weight: var(--weight);
    font-size: 38px; }
  body h2 {
    --weight: 600;
    font-weight: var(--weight);
    font-size: 32px; }
  body h3 {
    --weight: 500;
    font-weight: var(--weight);
    font-size: 28px; }
  body h4 {
    --weight: 500;
    font-weight: var(--weight);
    font-size: 28px; }
  body p {
    --weight: 400;
    font-weight: var(--weight);
    font-size: 18px; }
  body .section, body header {
    width: 100%;
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto; }
  body .section {
    padding: 4rem 0; }
  body .flex-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap; }
    body .flex-container.flex__center {
      justify-content: center; }
  body .f-w {
    width: 100%; }
  body input[type=text] {
    --raidus: 3px;
    height: 3rem;
    display: block;
    padding: 1rem;
    border: 1px solid var(--regularGrey);
    border-radius: var(--radius); }

.btn-custom {
  padding: .8rem 1.5rem;
  max-width: 166px;
  text-align: center;
  width: 70%;
  border: none;
  font-size: 16px;
  display: block;
  margin: 2rem 0;
  cursor: pointer;
  border-radius: 5px;
  background-color: var(--darkPink);
  color: var(--white); }
  .btn-custom.active {
    background-color: var(--vueGreen);
    color: var(--white); }
  .btn-custom:hover {
    --darkPink: #e22f5f;
    background-color: var(--darkPink); }

.metodo-wrapper {
  width: 50%; }
  .metodo-wrapper .metodo__code--metodo {
    --weight: 600;
    color: var(--vueGreen);
    font-size: 22px;
    font-weight: var(--weight); }
  .metodo-wrapper span.metodo__code--args {
    --darkPink: #fd8eab;
    color: var(--pink); }

.output {
  padding: 1rem;
  background-color: var(--darkBlue);
  display: inline-block;
  margin: 0;
  border-radius: var(--radius);
  color: var(--white); }

.resultado__output {
  color: var(--yellow); }

.explanation {
  padding: 1rem;
  border-radius: var(--radius);
  display: inline-block; }
  .explanation.success {
    background-color: var(--vueGreen);
    color: var(--white); }
  .explanation.warning {
    background-color: var(--yellow);
    color: var(--black); }
  .explanation .result__underline {
    --weight: 500;
    color: var(--darkBlue);
    font-weight: var(--weight);
    font-size: 20px; }

@media screen and (min-width: 992px) {
  body .flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
    body .flex-container.flex__center {
      justify-content: center; } }
